// verificar ganador en filas
const countInRow = (symbol, row) => row.filter(el => el === symbol).length;
const hasWinInRow = (symbol, row) => countInRow(symbol, row) === 3;

// verificar ganador en columnas
const countInColumn = (symbol, colNumber, ...rows) => rows.map(row => row[colNumber]).filter(el => el === symbol).length;
const hasWinInColumn = (symbol, colNumber, ...rows) => countInColumn(symbol, colNumber, ...rows) === 3;

// verificar ganador diagonal izquierda
const countInLeftSlant = (symbol, ...rows) => {
  const [row0, row1, row2] = rows;
  return [row0[0], row1[1], row2[2]].filter(el => el === symbol).length;
};
const hasWinInLeftSlant = (symbol, ...rows) => countInLeftSlant(symbol, ...rows) === 3;

// verificar ganador diagonal derecha
const countInRightSlant = (symbol, ...rows) => {
  const [row0, row1, row2] = rows;
  return [row0[2], row1[1], row2[0]].filter(el => el === symbol).length;
};
const hasWinInRightSlant = (symbol, ...rows) => countInRightSlant(symbol, ...rows) === 3;

export const hasWinSymbol = (symbol, board) => {
  const rows = Object.keys(board).map(row => board[row]);
  return [
    { line: 'row0', win: hasWinInRow(symbol, board[0]) },
    { line: 'row1', win: hasWinInRow(symbol, board[1]) },
    { line: 'row2', win: hasWinInRow(symbol, board[2]) },
    { line: 'column0', win: hasWinInColumn(symbol, 0, ...rows) },
    { line: 'column1', win: hasWinInColumn(symbol, 1, ...rows) },
    { line: 'column2', win: hasWinInColumn(symbol, 2, ...rows) },
    { line: 'leftSlant', win: hasWinInLeftSlant(symbol, ...rows) },
    { line: 'rightSlant', win: hasWinInRightSlant(symbol, ...rows) },
  ].reduce((answer, nextCheck) => (nextCheck.win ? nextCheck : answer), {
    win: false,
  });
};

export default hasWinSymbol;



